@use "../utilities" as u;

footer {
  position: relative;
  height: u.rem(50);
  background-color: u.$darkgrey-color;
  @include u.flexbox(row, flex-start, center);

  .footer-paragraph {
    color: u.$golden-color;
    font-size: u.rem(17);

    a {
      @extend .footer-paragraph;
      margin-left: u.rem(50);
    }
  }
}
