@import url("https://fonts.googleapis.com/css2?family=EB+Garamond:wght@400;500;700&display=swap");
.invisible {
  visibility: hidden;
}

.hidden {
  display: none;
}

*,
::after,
::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: 100%;
  width: 100%;
}

body {
  background-color: #1e2427;
  font-family: "EB Garamond", serif;
}

::selection {
  background-color: #c0a187;
  color: #1e2427;
}

::-webkit-scrollbar {
  width: 0.75rem;
}

::-webkit-scrollbar-thumb {
  background-color: #c0a187;
  border: 0.0625rem solid #1e2427;
  box-shadow: inset 0 0 0.375rem #1e2427;
  border-radius: 0.3125rem;
}

header {
  background-color: #414141;
  height: 3.125rem;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  position: relative;
}
header .navbar-logo::after {
  content: "";
  background: url(../../src/styles/icons/camera.svg) no-repeat;
  cursor: pointer;
  height: 2.0625rem;
  width: 2.0625rem;
  position: absolute;
  left: 1.0625rem;
  top: 0.4375rem;
}
header .navbar-item {
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.9375rem;
  font-weight: bold;
  color: #c0a187;
  margin-right: 0.875rem;
}
header .navbar-item:hover {
  opacity: 0.8;
}

main {
  padding: 1.25rem 1.25rem 0;
  margin-top: -3.125rem;
}

.total-homepage .container-result-heading-level {
  min-height: 100vh;
  max-width: 31.25rem;
  margin: 0 auto;
  padding: 10.625rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.total-homepage .container-result-heading-level .result {
  text-align: center;
  color: #c0a187;
}
.total-homepage .container-result-heading-level .result-user {
  font-size: 1.4375rem;
}
.total-homepage .container-result-heading-level .heading {
  text-align: center;
  color: #c0a187;
  line-height: 1.5;
}
.total-homepage .container-result-heading-level .heading-one {
  font-size: 1.625rem;
  text-transform: uppercase;
}
.total-homepage .container-result-heading-level .heading-two {
  font-size: 1.4375rem;
  margin-top: 0.875rem;
}
.total-homepage .container-result-heading-level .heading-two-level-word {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(to right, #d5ce97, #f5ea83, #f6dd50) text;
  font-size: 1.4375rem;
}
.total-homepage .container-result-heading-level .level {
  margin-top: 0.875rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 25.625em) {
  .total-homepage .container-result-heading-level .level {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}
.total-homepage .container-result-heading-level .level-basic, .total-homepage .container-result-heading-level .level-medium, .total-homepage .container-result-heading-level .level-advanced {
  cursor: pointer;
  margin: 1.25rem;
  border: 0.0625rem solid #c0a187;
  border-radius: 0.3125rem;
  width: 5.125rem;
  height: 1.875rem;
  font-size: 0.8125rem;
  font-weight: bold;
  font-family: "EB Garamond", serif;
  text-transform: uppercase;
}
.total-homepage .container-result-heading-level .level-basic {
  background-color: #d5ce97;
}
.total-homepage .container-result-heading-level .level-basic:hover, .total-homepage .container-result-heading-level .level-basic:active {
  box-shadow: 0 0 0.09375rem #d5ce97;
}
.total-homepage .container-result-heading-level .level-medium {
  background-color: #f5ea83;
}
.total-homepage .container-result-heading-level .level-medium:hover, .total-homepage .container-result-heading-level .level-medium:active {
  box-shadow: 0 0 0.09375rem #f5ea83;
}
.total-homepage .container-result-heading-level .level-advanced {
  background-color: #f6dd50;
}
.total-homepage .container-result-heading-level .level-advanced:hover, .total-homepage .container-result-heading-level .level-advanced:active {
  box-shadow: 0 0 0.09375rem #f6dd50;
}

.total-quiz .container-quiz {
  color: #c0a187;
  min-height: 100vh;
  max-width: 31.25rem;
  margin: 0 auto;
  padding: 10.625rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.total-quiz .container-quiz .timer-assertions-tracking {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 1.875rem;
}
.total-quiz .container-quiz .timer-assertions-tracking .timer-item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 2.5rem;
  width: 2.5rem;
  background-color: #c0a187;
  border-radius: 50%;
}
.total-quiz .container-quiz .timer-assertions-tracking .timer-item-seconds {
  color: #1e2427;
  font-size: 1.5rem;
}
.total-quiz .container-quiz .timer-assertions-tracking .assertions-tracking .assertions-numbers {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  font-size: 1.5rem;
}
.total-quiz .container-quiz .timer-assertions-tracking .assertions-tracking .assertions-numbers .each-assertion, .total-quiz .container-quiz .timer-assertions-tracking .assertions-tracking .assertions-numbers .slash, .total-quiz .container-quiz .timer-assertions-tracking .assertions-tracking .assertions-numbers .total-assertions {
  margin-left: 0.125rem;
}
.total-quiz .container-quiz .assertion-container {
  margin-bottom: 1.25rem;
  width: 100%;
}
.total-quiz .container-quiz .assertion-container .assertion {
  font-size: 1.5rem;
  font-weight: normal;
  text-align: center;
  height: 5rem;
  padding-top: 0.9375rem;
}
.total-quiz .container-quiz .answer {
  margin-bottom: 2.5rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: normal;
  width: 100%;
}
.total-quiz .container-quiz .answer-label {
  cursor: pointer;
  font-size: 1.375rem;
  margin: 0 1.25rem;
  padding-left: 1.5625rem;
  position: relative;
}
.total-quiz .container-quiz .button-container {
  text-align: center;
  margin-bottom: 1.875rem;
  width: 100%;
}
.total-quiz .container-quiz .button-container .button-submit-js {
  cursor: pointer;
  padding: 0.125rem 0.3125rem 0.1875rem 0.3125rem;
  border-radius: 0.25rem;
  border: 0.0625rem solid #c0a187;
  color: #c0a187;
  font-family: "EB Garamond", serif;
  font-size: 1.1875rem;
  background: transparent;
}
.total-quiz .container-quiz .button-container .button-submit-js:hover {
  opacity: 0.9;
}

.info {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.info .tooltip-about-container {
  background-color: #1e2427;
  color: #c0a187;
  border: 0.0625rem solid #c0a187;
  border-top-right-radius: 0.3125rem;
  border-bottom-right-radius: 0.3125rem;
  max-width: 15.625rem;
  height: 37.5rem;
  padding: 0 1.25rem;
  line-height: 1.7;
  overflow: auto;
  position: absolute;
  top: 3.125rem;
  left: 0;
  border-left: none;
  animation-name: showModal;
  animation-duration: 0.4s;
}
@keyframes showModal {
  0% {
    left: -50%;
  }
}
@media (min-width: 43.75em) {
  .info .tooltip-about-container {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-left: 0.0625rem solid #c0a187;
    border-radius: 0.3125rem;
    max-width: 37.5rem;
    height: 25rem;
  }
  @keyframes showModal {
    0% {
      top: -50%;
    }
  }
}
.info .tooltip-about-container .close-btn {
  font-size: 2.1875rem;
  position: absolute;
  left: 0.625rem;
  top: -0.3125rem;
  cursor: pointer;
}
.info .tooltip-about-container .close-btn:hover {
  transform: scale(1.03);
  color: #ffffff;
}
.info .tooltip-about-container .about {
  padding: 0.625rem;
  margin-top: 1.25rem;
}
.info .tooltip-about-container .about-title {
  text-align: center;
  text-transform: uppercase;
  font-size: 1.5625rem;
  padding: 0.625rem 0;
}
.info .tooltip-about-container .about-paragraph {
  font-size: 1.3125rem;
}
.info .tooltip-about-container .tooltip {
  padding: 0.625rem;
  margin: 1.25rem 0;
}
.info .tooltip-about-container .tooltip-title {
  text-align: center;
  text-transform: uppercase;
  font-size: 1.5625rem;
  margin-bottom: 0.625rem;
}
.info .tooltip-about-container .tooltip-paragraph {
  font-size: 1.3125rem;
}
.info .tooltip-about-container .tooltip-paragraph code {
  text-align: left;
  padding: 0.25rem;
  color: #000000;
  background-color: #d8d854;
  border-radius: 0.3125rem;
  line-height: 2.4;
}
.info .tooltip-about-container .tooltip-paragraph code::selection {
  background: transparent;
}

/* ((create custom radio buttons)) */
.radio-circle::after {
  content: "";
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  background-color: #ffffff;
  position: absolute;
  top: 0.25rem;
  left: -0.0625rem;
}

/* ((show the user's choice by coloring radio buttons)) */
input[type=radio]:checked ~ .radio-circle::after {
  background: radial-gradient(circle at center, #000000, #ffffff 40%);
}

footer {
  position: relative;
  height: 3.125rem;
  background-color: #414141;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
footer .footer-paragraph, footer .footer-paragraph a {
  color: #c0a187;
  font-size: 1.0625rem;
}
footer .footer-paragraph a {
  margin-left: 3.125rem;
}